.apps {
    width: auto;
    max-height: 200px;
    overflow: scroll;
}
.apps::-webkit-scrollbar{
    display:none;
}
.apps>li {
    height: 45px;
    list-style-type: none;
    cursor: default;
}
.apps>li:hover {
    background-color: rgb(217, 217, 217);
}
.app {
    width:  100%;
    height:  100%;
}
.appIcon {
    display:  inline-block;
    float:  left;
    height:  38px;
    width:  38px;
    position:  relative;
    top:  50%;
    margin-top:  -19px;
    border-radius:  8px;
    line-height:  38px;
    text-align:  center;
    color:  #ffffff;
    font-size:  30px;
}

.appName {
    display: inline-block;
    float: left;
    margin-left: 10px;
    height: 38px;
    position: relative;
    top: 50%;
    margin-top: -19px;
    font-size: 20px;
    color: rgb(23, 23, 23);
}
